      Todo: add logic deterimine start and end
    */

    this.end = new Date().toISOString().split('T')[0];
    this.day.setDate(this.day.getDate()-7);
    this.start =  this.day.toISOString().split('T')[0]
    this.populateData('0')
    
  }
    private handleClick(tab){
        
